@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background-color: var(--background-color);
    font-family: poppins;
    overflow-x: hidden;
    max-width: 125rem;
    margin: auto;
}
:root{
    --light-blue: #f2fbff;
    --blue: #1616de;
    --color-black: #000000;
    --color-white: #ffffff;
    --navbar-shadow: #b5b5b5;
    --gray-color: #696969;
}
*{
    box-sizing: border-box;
    margin: 0;
}
ul{
    margin: 0px;
    padding: 0px;
}
.inline{
    display: inline;
}
.inline-block{
    display: inline-block;
}
.flex-center-horizontal-vertical{
    display: flex;
    justify-content: center;
    align-items: center;
}
footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-color);
    width: 100%;
    min-height: 12.5rem;
    color: var(--color-white);
}
.footer-icons-container{
    display: flex;
    justify-content: space-around;
    width: 16rem;
    margin: 1rem 0;
}
.footer-icons-container .footer-icon{
    font-size: 2.5rem;
    cursor: pointer;
}
.footer-icons-container .footer-icon:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.navbar{
    box-shadow: 0px 5px 3px 0px var(--navbar-shadow);
}
.navbar-icons-container{
    min-width: 11rem;
}

.quantity-btn-container{
    display: flex;
    align-items: center;
    margin: 1rem 0;
}
.quantity-btn{
    cursor: pointer;
    background-color: transparent;
}
.quantity-info{
    display: inline-block;
    border: 1px solid var(--color-gray);
    font-size: 1.3rem;
    padding: 2px 1rem;
    margin: 0 0.5rem;
}
.quantity-btn i{
    font-size: 2rem;
}
.navbar-icon{
    font-size: 2.2rem;
}
.hamburger-icon{
    font-size: 2.4rem;
}
.button-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2px;
    font-size: 1.3rem;
}
.hero-section{
    position: relative;
    max-height: 43.7rem;
}
.hero-section img{
    max-height: 43.7rem;
}
.shop-btn{
    display: inline-block;
    margin: 0.8rem 0;
}
.hero-section a{
    display: flex;
    justify-content: center;
}
.overlay-container{
    position: absolute;
    left: 25%;
    top: 35%;
}
.overlay-container h1{
    -webkit-animation: infinite;
            animation: infinite;
    -webkit-animation-name: move;
            animation-name: move;
    -webkit-animation-duration: 3.5s;
            animation-duration: 3.5s; 
}
@media only screen and (max-width: 960px){
    .overlay-container{
        left: 15%;
        top: 25%;
    }   
    .overlay-container h1{
        font-size: 3rem;
    }
}
@media only screen and (max-width: 730px){
    .overlay-container{
        left: 5%;
        top: 20%;
    }   
    .hero-section{
        position: relative;
        min-height: 50vh;
    }
    .hero-section img{
        min-height: 50vh;
    }
}
@media only screen and (max-width: 450px){
    .overlay-container h1{
        font-size: 2rem;
    }  
}
@-webkit-keyframes move {
    0%   {margin-left:50px;}
    100% {margin-left: 0px;}
}
@keyframes move {
    0%   {margin-left:50px;}
    100% {margin-left: 0px;}
}
.new-arrivals-section{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.card{
    max-width: 50rem;
}
.card-hover{
    cursor: pointer;
    transition: 0.3s;
}
.card-hover:hover{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.card-new-arrival{
    cursor: pointer;
    transition: 0.3s;
    margin: 1rem 1rem;
}
.card-new-arrival .text-overlay{
    background-color: rgba(33,37,41,0.2);
}
.card-only-image {
    max-width: 600px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.text-overlay-text-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 80%;
    height: 50%;
    background-color: white;
}
.card-new-arrival:hover{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.home-section-width{
    max-width: 87.5rem;
    margin: auto;
}
.home-section-heading{
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    margin-bottom: 1rem;
}
.home-section-heading::after{
    content: "";
    width: 6rem;
    background-color: var(--primary-color);
    height: 4px;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    bottom: -12px;
    border-radius: 1rem;
}
.section-wrapper{
    background-color: var(--primary-color);
}
@media only screen and (max-width: 1240px){
    .spacer-3{
        height: 2rem;
    }
}
@media only screen and (max-width: 450px){
    .home-section-heading{
        font-size: 2rem;
    }
    .spacer-3{
        height: 1rem;
    }
}
.featured-products-section {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
}
.card-vertical{
    max-width: 25rem;
    width: 100%;
}
@media only screen and (max-width: 345px){
    .card-vertical {
        margin: 0 2rem;
    }
    .card-vertical .image-container {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
}

.featured-brands-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.featured-brands-container img{
    width: 18.75rem;
    height: 11.25rem;
    object-fit: cover;
}
.featured-brand-card{
    width: 25rem;
    height: 20rem;
}
.featured-brand-image{
    border-radius: 1rem;
  
}
.products-section{
    display: grid;
    grid-template-columns: 19rem auto;
    position: relative;
    margin: 0rem;
}
.products-container{
    min-height: 100vh;
    grid-column: 2 / 3;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    grid-gap: 3rem;
    gap: 3rem;
}
@media only screen and (max-width: 800px){
    .products-container{
        grid-column: 1 / 3;
    }
}
.card{
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.card-vertical {
    max-width: 25rem;
    width: 100%;
}
.card-hover{
    cursor: pointer;
    transition: 0.3s;
}
.card-hover:hover{
    box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
}
.discount-percent {
    font-weight: 550;
    margin-left: 0.2rem;
}
@media only screen and (max-width: 345px){
    .card-vertical{
        margin: 0 2rem;
    }
    .card-vertical .image-container {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
}
.sidebar{
    position: absolute;
    top: 0px;
    height: 100%;
}
.sidebar-filter-label{
    margin-left: 6px;
}
.filters{
    color: var(--color-white);
    margin: 2.5rem 1rem 0rem 1rem;
}
.filter-type{
    border-bottom: 1px solid var(--color-white);
    margin-bottom: 1rem;
}
.filter-list li{
    margin: 0.5rem 0rem;
}
.filter-heading{
    display: flex;
    justify-content: space-between;
}
.filter-heading span{
    cursor: pointer;
}
.filter-heading span:hover{
    font-weight: 500;
}
.sidebar-filter-item{
    display: flex;
    align-items: center;
}
.sidebar-filter-input{
    width: 1.1rem;
    height: 1.1rem;
}
.my-cart-card{
    /* This padding is to override the css coming from CL */
    padding: 0rem 0rem;
    max-width: 38rem;
    border-radius: 3px;
    margin: 2rem 0rem;
}
.my-cart-card .image-container{
    min-width: 10rem;
    max-width: 16rem;
}
.my-cart-card .image-container img{
    border-radius: 3px;
}
.my-cart-card .wrapper{
    margin: 1rem 1rem;
}

.my-cart-cta-btn-container{
    max-width: 32rem;
    width: 100%;    
}
.my-cart-cta-btn-container button{
    margin: 0.4rem 0rem;
}
.my-cart-cta-btn{
    display: block;
    width: 100%;
}
.mycart-container{
    max-width: 65rem;
    width: 90%;
    margin: auto;
}
.mycart-items-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 1 / 2;
}
.mycart-summary-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 2 / 3;
}


/* Price details Secction css */
.price-details-card{
    margin: 2rem 0rem;
    border-radius: 3px;
}
.price-details-card-heading{
    border-bottom: 2px solid gray;
    padding-bottom: 0.4rem;
}
.price-details-item{
    display: flex;
    justify-content: space-between;
}
.price-details-section{
    border-bottom: 2px solid gray;
}
.price-details-card .btn-container{
    display: block;
    margin: 1rem 0rem 0rem 0rem;
}
.price-details-card .btn-container button{
    width: 100%;
    margin: 0rem;
}
.empty-cart-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}
.empty-cart-img{
    max-width: 400px;
    max-height: 350px;
    object-fit: contain;
}
@media only screen and (max-width: 1028px){
    .grid-50-50{
        grid-template-columns: 100%;
        grid-template-rows: auto;
    }
    .mycart-items-container{
        grid-column: 1;
    }
    .mycart-summary-container{
        grid-column: 1;
    }
}
.wishlist-products{
    min-height: 50vh;
}
.wishlist-products-container{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 2rem;
}
.wishlist-cart-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}
.wishlist-cart-img{
    max-width: 25rem;
    max-height: 21rem;
    object-fit: contain;
    margin: 0 1rem 1rem 1rem;
}

.authentication{
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: var(--light-blue);
    border-radius: 8px;
    width: 90%;
    max-width: 93.7rem;
    margin: 0rem auto;
    padding: 5rem 1rem;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    grid-gap: 1rem;
    gap: 1rem;
}
.wrapper{
    position: relative;
}
.blob{
    position: absolute;
    width: 30%;
    z-index: -1;
}
.blob1{
    top: -120px;
    left: -100px;
}
.blob2{
    bottom: -100px;
    right: -100px;
}
.remember-me-checkbox{
    margin-left: 4px;
}
.authentication-image-container{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 1 / 2;
    z-index: 2;
    width: 100%; 
}
.authentication-image-container img{
    width: 100%;
    max-width: 40rem;
}
.authentication-form-container{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 2 / 3;
    z-index: 2;
}
.authentication-form{
    background-color: white;
    max-width: 31.2rem;
    width: 100%;
    border-radius: 6px;
    padding: 1rem 0.5rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.authentication-form-heading{
    text-align: center;
    margin: 0 1rem 0.3rem 1rem;
}
.authentication-form-sub-heading{
    margin: 0.3rem 1rem;
    text-align: center;
}
.form-input-box{
    position: relative;
    display: flex;
    justify-content: center;
    margin: 1.8rem 0rem;
}
.form-input-box input{
    width: 90%;
    padding-left: 1rem;
    font-size: 1rem;
    height: 3.12rem;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border-radius: 3px;
    border: 2px solid #BCBCBC;
}
.form-input-box input:focus{
    outline: none;
    border: 2px solid var(--primary-color);
}
.form-input-box i{
    position: absolute;
    top: 0.68rem;
    right: 2.81rem;
    cursor: pointer;
    font-size: 1.5rem;
}
.mail-icon{
    position: absolute;
    top: 0.80rem;
    right: 2.81rem;
    cursor: pointer;
    font-size: 1.5rem;
}
.checkbox-container{
    width: 90%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto 1.8rem auto;
}
.forgot-password-btn{
    color: var(--color-black);
    background-color: transparent;
    cursor: pointer;
}
.forgot-password-btn:hover{
    color: var(--primary-color);
}
.form-cta-button{
    display: block;
    width: 90%;
    margin: 1rem auto;
    border-radius: 3px;
    text-align: center;
}
.login{
    padding: 0;
}
.forgot-password{
    padding: 0;
}
.logout-container{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
}
.logout-btn-container{
    width: 100%;
    display: flex;
    justify-content: center;
}
@media only screen and (max-width: 1024px){
    .authentication{
        grid-template-columns: 100%;
        grid-template-rows: auto;
    }
    .authentication-image-container{
        grid-column: 1 / 1;
        grid-row: auto;
        z-index: 2;
        margin: 1rem auto;
    }
    .authentication-image-container img{
        max-width: 30rem;
        margin-right: 1.5rem;
    }
    .authentication-form-container{
        grid-column:1 / 1;
        grid-row: auto;
        z-index: 2;
        margin: 1rem;
    }
}
.toasts-container{
    z-index: 1000;
    position: fixed;
    top: 1rem;
    right: 10px;
}
.toast{ 
    min-width: 12.5rem;
    margin: 1rem 0;
}
.toast-text{
    flex: 1 1;
}
.toast-info{
    background-color: var(--blue);
    color: white;
}
.toast-close-icon{
    cursor: pointer;
    font-size: 1.3rem;
    margin-left: 0.9rem;
}
.profile-container{
    min-height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.profile{
    width: 100%;
    max-width: 50rem;
    margin: 2rem;
    padding: 1rem;
    border: 2px solid var(--primary-color);
    border-radius: 5px;
}
.profile-btn-container{
    display: flex;
    justify-content: center;
    margin: 0 2rem;
    padding: 0 0 1rem 0;
    border-bottom: 2px solid var(--primary-color);
}
.profile-btn-wrapper{
    width: 15.6rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.profile-btn-wrapper button{
    width: 7.5rem;
    margin: 0.5rem 0;
}
.profile-btn-wrapper a{
    display: flex;
    justify-content: center;
    width: 7.5rem;
    margin: 0.5rem 0;
}
.user-profile-container{
    display: grid;
    grid-template-columns: 14.3rem 1fr;
    margin-top: 1rem;
}
.user-info-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 28.1rem;
}
.user-info-item{
    display: flex;
    justify-content: space-between;
    margin: 0.4rem;
}

/* Address Item */
.address-item{
    margin: 1rem 2rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--gray-color);
}
.address-btn-container{
    margin: 0.5rem 0;
}
@media only screen and (max-width: 720px){
    .user-profile-container{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
        justify-content: center;
        margin: 2rem;
    }   
    .user-image-container{
        display: flex;
        justify-content: center;
    }
    .user-info-container{
        max-width: 100%;
    }
}
